import { Radio, RadioGroup, Stack, Text } from '@chakra-ui/react';
import React from 'react';

const Params = ({value, setValue}) => {
  return (
   <RadioGroup 
      onChange={setValue} value={value} 
      h={'252px'} bgColor={'#FFFFFF'} alignSelf={'start'} p={5} borderRadius={'5px'} boxShadow={'0px 2px 8px rgba(0, 0, 0, 0.1)'}
    > 
      <Text mb={4}> </Text>
      <Stack direction={'column'}>
        <Radio value="All"></Radio>
        <Radio value="NoTransfer"> </Radio>
        <Radio value="OneTransfer">1 </Radio>
        <Radio value="TwoTransfers">2 </Radio>
        <Radio value="ThreeTransfers">3 </Radio>
      </Stack>
   </RadioGroup> 
  );
};

export default Params;
